<template>
  <div class="covid-list-view">
    <h1>COVID Analysis Pages</h1>
    <div class="pages-container">
      <div v-for="page in covidPages" :key="page.filename" class="page-item">
        <h3>{{ page.title }}</h3>
        <p class="category">{{ page.category }}</p>
        <button @click="loadPage(page.filename)" class="load-btn">View Page</button>
      </div>
    </div>
    
    <div v-if="selectedPageContent" class="page-content">
      <h2>Page Content</h2>
      <div v-html="selectedPageContent" class="content"></div>
    </div>
  </div>
</template>

<script>
import PagesService from '@/services/pagesService'

export default {
  name: 'CovidList',
  data() {
    return {
      covidPages: [],
      selectedPageContent: null
    }
  },
  async mounted() {
    const allPages = await PagesService.getAllPages()
    this.covidPages = allPages.filter(page => 
      page.category.toLowerCase().includes('covid') || 
      page.title.toLowerCase().includes('covid')
    )
  },
  methods: {
    async loadPage(filename) {
      try {
        const content = await PagesService.loadPageContent(filename)
        this.selectedPageContent = PagesService.markdownToHtml(content)
      } catch (error) {
        console.error('Error loading page:', error)
        this.selectedPageContent = '<p>Error loading page content.</p>'
      }
    }
  }
}
</script>

